<template>
    <born-obstetrical-history-form :isInBornPage="true" />
    <born-ovarian-form :isInBornPage="true" />
    <born-oocyte-collection :isInBornPage="true" />
    <born-fresh-embryo :isInBornPage="true" />
    <bron-frozen-embryo :isInBornPage="true" />
    <p-button label="Save" @click="save" />
</template>

<script>
import BornObstetricalHistoryForm from "@/components/born/obstetrical-history-form.vue";
import BornOvarianForm from "@/components/born/ovarian-form.vue";
import BornOocyteCollection from "@/components/born/oocyte-collection-form.vue";
import BornFreshEmbryo from "@/components/born/fresh-embryo.vue";
import BronFrozenEmbryo from "@/components/born/frozen-embryo.vue";
import { useStore } from "vuex";
import { onMounted, provide, reactive } from "vue-demi";
import { useToast } from "primevue/usetoast";

export default {
    components: {
        BornObstetricalHistoryForm,
        BornOvarianForm,
        BornOocyteCollection,
        BornFreshEmbryo,
        BronFrozenEmbryo
    },
    setup() {
        const store = useStore();

        const bornForm = reactive({
            obstetricalHistory: null,
            ovarian: null,
            oocyteCollection: null,
            freshEmbryo: null,
            frozenEmbryo: null
        });
        provide("bornForm", bornForm);

        const toast = useToast();

        const save = () => {
            store
                .dispatch("born/updateAllBornPart", {
                    patientId: store.state.artCycle.patient.id,
                    cycleId: store.state.artCycle.selectedCycle.cycleId,
                    ...bornForm.obstetricalHistory,
                    ...bornForm.ovarian,
                    ...bornForm.oocyteCollection,
                    ...bornForm.freshEmbryo,
                    ...bornForm.frozenEmbryo
                })
                .then(() => {
                    toast.add({
                        severity: "success",
                        summary: "Succes",
                        detail: "Saved Succesfully",
                        life: 3000
                    });
                })
                .catch(() => {});
        };

        onMounted(() => {
            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );
        });

        return { save };
    }
};
</script>
